@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap");

html,body{
	font-family: 'Noto Sans KR', sans-serif;
	font-weight: 400;
  font-size:10px;
}
body {
	line-height: 1.5;
}
html, body, div, span, h1, h2, h3, h4, h5, h6, p, a,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, figure, figcaption, footer, header,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}

ol, ul, ol li, ul li {
	list-style: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}
a{text-decoration: none; color: #000;}
strong{font-weight: bold;}
button{background-color: transparent; cursor: pointer; border: none;}
legend,caption{position: absolute; top:0; left: 0; width: 0; height: 0; line-height: 0; visibility: hidden; overflow: hidden; text-indent: -9999px;}
.blind{position: absolute; top:0; left: 0; width: 0; height: 0; line-height: 0; visibility: hidden; overflow: hidden; text-indent: -9999px;}
textarea{resize: none; font-family: 'Noto Sans KR', sans-serif;}
a, button{font-family: 'Noto Sans KR', sans-serif; font-size:14px;}
img{width:100%; height:100%;}
body.scroll_hidden{overflow-y: hidden;}
i,em{font-style: normal;}


:root{
  --cont-width:1200px;
  --lnner-left-width:865px;
  --lnner-right-width:270px;
  --cont-top:120px;
  --primary-color:#00befc;
  --c-gray:#666;
  --c-light-gray:#939393;
  --c-gradient: linear-gradient(85.66deg, #01affe 33.71%, #4bde97 75.67%);
  --c-orange:#fc7900;
  --c-boldgreen:#23425c;
  --red-color:#ff0303;
  --form-border:#ccc;
  --txt-lh:1.4;
  --cont-gap:60px;
  --base-gap:40px;
  --bd-radius:30px;
  --trans-3s:all ease 0.3s;
}

